// const baseUrl = 'https://baiyuechangxiong-api.luobo.info' // 
const baseUrl = 'https://carshop.duoka361.cn' //  
let timestamp = Math.round(new Date().getTime() / 1000).toString() //
// let secret = 'c8bef9c31576a5e7c48dcb27a558cb8c'
// let sign = md5(secret + timestamp) //MD5
import { getWXJsCode, userInfo, getOpenId } from '@/utils'

import { getOpenIds } from '@/api/user'

//Promise
export default function request(url, params, method = 'GET',contentType) {
    return new Promise((reslove, reject) => {
        uni.request({
            url: baseUrl + url,
            method: method || 'GET',
            header: {//
				'Content-Type': contentType || 'application/x-www-form-urlencoded',
                'timestamp': timestamp,
                'access-token':uni.getStorageSync('token'),
				'openId': uni.getStorageSync('openId'),
            },
            data: params || {},
            success: (res) => {
				// console.log(res)
				var datas = res.data;
                if (datas.code == '1') {
                    reslove(datas);
                } else if(datas.code == '-1') {
					reslove(datas);
				}else if (datas.code == '30000') {// 
					getWXJsCode().then(async function(userCode){
						console.log('token')
						uni.showLoading();
						let { bindStatus, openId, accessToken } = await getOpenIds({ code: userCode });
						console.log('request',openId)
						uni.setStorageSync('bindStatus',bindStatus);
						uni.setStorageSync('openId',openId);
						uni.setStorageSync('token',accessToken);
						// setInterval(() => {
							uni.hideLoading();
							// 
							const pages = getCurrentPages()
							// pagesgetCurrentPages
							const curPage = pages[pages.length - 1]
							// 
							curPage.onLoad(curPage.options) // 
							curPage.onShow()
							curPage.onReady()
							// clearInterval()
						// }, 1000)
					})
                }else if(datas.code == '500'){
					// console.log(datas)
					reslove(datas) 
				} else if (datas.code !== '1' && datas.code !== '30000'&& datas.code !== '500') {// 
					uni.showToast({
                        title: datas.message || '',
                        icon: 'none',
                        duration: 2000
                    });
                    reject(datas.message || '');
                }
            },
            fail: (msg) => {
                reject(msg || '');
            }
        })
    });
}
